.loader {
    width: 100px;
    height: 100px;
    border-radius: 100%;
    margin: 30vh auto;
    border: 2px dashed #299DED;
    animation-name: loading;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes loading{
    0% {
        transform: rotate(0deg);

    }
    100% {
        transform: rotate(360deg) scale(2);
    }
}
